<template>
  <v-container fluid fill-height>
    <v-layout align-center justify-center v-if="!authenticated">
      <v-flex xs12 sm8 md4>
        <v-card class="elevation-4 pa-4">
          <v-card-title class="text-h5 text-md-h4 font-weight-black">
            Login
          </v-card-title>
          <v-card outlined color="light-blue lighten-4">
            <v-card-subtitle class="font-weight-black">Important Notice</v-card-subtitle>
            <v-card-text>
              <ul>
                <li>If you enter your credentials incorrectly three times, your account will be locked.</li>
                <li>If your account is locked, you may wait 45 seconds before logging in again.</li>
                <li>Please contact ITSD if you have forgotten your credentials.</li>
              </ul>
            </v-card-text>
          </v-card>
          <v-card-text>
            <v-alert dismissible text type="error" v-if="invalidCredentials"
              >Invalid credentials.</v-alert
            >
            <v-form ref="form" @submit.prevent="handleSubmit">
              <v-text-field
                ref="username"
                prepend-icon="mdi-account"
                name="username"
                label="Username"
                type="text"
                v-model="form.username"
                :rules="[rules.required]"
                :disabled="loading"
              ></v-text-field>
              <v-text-field
                id="password"
                prepend-icon="mdi-lock"
                name="password"
                label="Password"
                v-model="form.password"
                :rules="[rules.required]"
                :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                :type="showPassword ? 'text' : 'password'"
                @click:append="showPassword = !showPassword"
                :disabled="loading"
              ></v-text-field>
              <v-btn
                block
                class="mt-4"
                color="primary"
                type="submit"
                :loading="loading"
                :disabled="disable"
                >Login</v-btn
              >
            </v-form>
          </v-card-text>
        </v-card>
        <div class="d-flex mt-5">
          <a href="#" class="mr-5">Help</a>
          <a href="#" class="mr-5">Privacy</a>
          <a href="#">Terms</a>
        </div>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import { mapGetters } from "vuex";
import formRules from "@/helpers/formRules";

export default {
  name: "Login",

  data: () => ({
    rules: formRules,
    form: {
      username: null,
      password: null,
    },
    showPassword: false,
    disable: false,
  }),

  computed: {
    ...mapGetters("auth", [
      "loading",
      "authenticated",
      "isLoggedIn",
      "invalidCredentials",
    ]),
  },

  created() {
    this.getRecord();
  },

  methods: {
    getRecord() {
      this.$store.commit("auth/setInvalidCredentials", false);
      setTimeout(() => {
        this.$refs.username.focus();
      }, 200);
    },

    handleSubmit() {
      if (this.$refs.form.validate()) {
        if (!this.loading) {
          this.$store.dispatch("auth/login", this.form);
        }
      }
    },
  },

  watch: {
    isLoggedIn(val) {
      if (val == true) {
        this.disable = true;
      }
    },

    authenticated(val) {
      if (val) {
        this.$router.replace({ name: "Dashboard" });
      }
    },
  },
};
</script>